#camModSc {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.cameraSprite {
    width: 400px;
    height: 400px;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: elemEnter-scale .5s forwards var(--sleek);

}

.cameraDoor {
    z-index: 1;
    width: 300px;
    height: 300px;
    background: rgb(192, 192, 192);
    display: flex;
    justify-content: center;
    padding-top: 20px;
    cursor: pointer;
    transform-origin: bottom;
}

.cameraDoorOpened {
    cursor: auto;
    animation: openDoor 1s forwards;
}

.cameradoorClosed {
    cursor: auto;
    animation: closeDoor .5s forwards;
}

@keyframes openDoor {
    0% {
        transform: rotateX(0deg);
    }

    36% {
        transform: rotateX(100deg);
    }

    54% {
        transform: rotateX(95.02deg);
    }

    74% {
        transform: rotateX(100deg);
    }

    82% {
        transform: rotateX(98.75deg);
    }

    92% {
        transform: rotateX(100deg);
    }

    96% {
        transform: rotateX(99.36deg);
    }

    100% {
        transform: rotateX(100deg);
    }
}

@keyframes closeDoor {
    from {
        transform: rotateX(100deg);
    }
    to {
        transform: rotateX(0deg);
    }
}

.cameraDoorLatch {
    background: red;
    height: 10px;
    width: 60px;
    transform: translateY(0px);
    transition: .3s transform var(--bounce);
}

.cameraDoorLatchPoked {
    transform: translateY(10px);
}

.cameraDoorLatchBound {
    position: absolute;
    height: 20px;
    width: 60px;
    outline: 1px solid blue;
}

.cartridgeWrap {
    position: absolute;
    display: flex;
    flex-direction: column;
    transition: .5s transform var(--anchorhead);
}

.cartridge {
    background: purple;
    height: 290px;
    width: 290px;
}

.ink {
    z-index: -1;
    position: absolute;
    background: pink;
    height: 100px;
    width: 290px;
    transition: .5s transform var(--anchorhead);
}

.modding {
    transform: translateX(400px);

    & .ink {
        transform: translateY(-130px);
    }
}